/* eslint-disable react/jsx-filename-extension */
/* eslint-disable react/prop-types */

import React from "react";
import Fade from "react-reveal/Fade";
import NotFound from "assets/images/NotFound.png";
import Button from "elements/Button";

export default function ServiceDetail({ data }) {
    if (data === null) {
        return (
            <section className="container mx-auto">
                <Fade bottom>
                    <div className="flex flex-col w-full justify-center">
                        <div className="flex w-full justify-center">
                            <img
                                src={NotFound}
                                alt="Not Found"
                                className="sm:w-3/4 xl:w-5/12 mt-5"
                            />
                        </div>
                        <h1 className="text-theme-deep-green text-2xl text-center mt-5">
                            Service Not Found
                        </h1>
                        <div className="flex justify-center">
                            <Button
                                href="/Service"
                                type="link"
                                className="flex w-30 h-10 px-5 mt-5 bg-theme-deep-green text-white items-center rounded transform transition duration-500 hover:bg-gray-900"
                            >
                                Go Back
                            </Button>
                        </div>
                    </div>
                </Fade>
            </section>
        );
    }

    return (
        <section className="container mx-auto">
            <Fade bottom>
                <Button
                    type="link"
                    href="/Service"
                    className="flex w-40 h-8 text-lg items-center ml-6 sm:ml-20 mt-8 font-light text-gray-400 hover:underline"
                >
                    <svg
                        className="w-5 h-5 text-gray-400 mr-1"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                    >
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M15 19l-7-7 7-7"
                        />
                    </svg>
                    See All Service
                </Button>
            </Fade>

            {data.map((item, index) => (
                <div className="flex flex-col mt-8 justify-center" key={index}>
                    <Fade bottom>
                        <h1 className="text-5xl text-theme-deep-green text-center font-bold">
                            {item.title}
                        </h1>

                        <p className="font-light text-xl text-gray-400 text-center mb-10">
                            {item.type}
                        </p>
                    </Fade>

                    <Fade bottom delay={300 * 1}>
                        <div className="flex justify-center xl:mb-6">
                            <img
                                src={item.imageUrl}
                                alt="Service"
                                className="flex w-4/5 sm:w-4/6"
                            />
                        </div>
                    </Fade>

                    <Fade bottom delay={300 * 1}>
                        <div className="flex flex-col mt-16 mb-12 mx-8 sm:mx-16 xl:mx-28">
                            <h1 className="text-3xl text-theme-deep-green font-bold mb-3">
                                Service Detail
                            </h1>
                            <p className="font-light text-lg text-gray-400 text-justify">
                                {item.description}
                            </p>
                        </div>

                        <div className="flex flex-col mx-8 sm:mx-16 xl:mx-28">
                            <h1 className="text-3xl text-theme-deep-green font-bold mb-5">
                                Teknologi Yang Kami Gunakan
                            </h1>

                            <div className="flex flex-row ml-1 flex-wrap">
                                {item.tool.split(', ').map((tool, idx) => (
                                    <div key={idx} className="mr-4 mb-2 px-6 py-3 text-theme-green border border-theme-green rounded-full shadow-lg">
                                        {tool}
                                    </div>
                                ))}
                            </div>
                        </div>
                    </Fade>
                </div>
            ))}
        </section>
    );
}
